import React from "react"
import { Link } from "gatsby"
import { PaperAirplaneIcon } from "@primer/octicons-react"
import Layout from "../components/layout"
import Col from "react-bootstrap/Col"
import Main from "../components/indexPage/Main"
import SEO from "../components/seo"
import Flash from "react-reveal/Flash"

const AboutPage = () => {
  return (
    <Layout>
      <SEO title="About" />
      <section className="row mx-0">
        <Col
          style={{
            boxShadow: "0 3px 8px #646464",
            paddingTop: "100px",
            paddingBottom: "50px",
          }}
          className={`text-center mx-auto wfBackground`}
        >
          <h2 className="mb-0 mx-auto display-4">Hello I'm Sungharsh</h2>
          <hr className="mx-5 mt-0" style={{ background: "#f15842" }} />
          <Flash delay={1000}>
            <Link
              // href="https://sungharsh.github.io/"
              to="/Contact"
              type="button"
              role="button"
              className="btn btn-outline-light btn-main text-monospace customBtn shadow pr-3"
            >
              <span className={`badge badge-pill pl-1`}>
                <PaperAirplaneIcon size={16} aria-label="Design icon" />
              </span>
              <strong>CONTACT </strong>
            </Link>
          </Flash>
        </Col>
      </section>
      <section className="row mx-0">
        <Col className="px-0">
          <Main />
        </Col>
      </section>
    </Layout>
  )
}

export default AboutPage
